/*TOOLTIP*/
$cubic: cubic-bezier(.64,.09,.08,1);


[class*="tooltip"] {
  position: relative;
  &:after {
    opacity: 0;
    visibility: hidden;
    position: absolute;
    content: attr(data-tooltip);
    padding: 1px 10px;
    top: 2.6em;
    left: 50%;
    color: $colormain;
    font-weight: 400;
    font-size: px_to_rem(11px);
    transform: translateX(-50%) translateY(-2px);
    background: #f1f1f1;
    border: 1px solid #c0c0c0;
    white-space: nowrap;
    z-index: 2;
    transition: opacity 0.2s $cubic, transform 0.2s $cubic;
  }
  &:hover {
    &:after {
      display: block;
      opacity: 1;
      visibility: visible;
      transform: translateX(-50%) translateY(0);
    }
  }
}

.tooltip--top:hover:after {
    top: -3px;
    transform: translate(-90%, -100%);
}

.tooltip--left {
  &:after {
    top: -4px;
    left: 0;
    transform: translateX(-112%) translateY(0);
  }
  &:hover {
    &:after {
      transform: translateX(-110%) translateY(0);
    }
  }
}

.tooltip--right {
  &:after {
    top: -4px;
    left: 100%;
    transform: translateX(12%) translateY(0);
  }
  &:hover {
    &:after {
      transform: translateX(10%) translateY(0);
    }
  }
}

.tooltip--triangle {
  &:before {
    content: '';
    width: 0;
    height: 0;
    border-left: solid 5px transparent;
    border-right: solid 5px transparent;
    border-bottom: solid 5px grey;
    opacity: 0;
    visibility: hidden;
    position: absolute;
    transform: translateX(-50%) translateY(-2px);
    top: 1.1em;
    left: 50%;
    transition: opacity 0.2s $cubic, transform 0.2s $cubic;
    z-index: 9;
  }
  &:hover {
    &:before {
      display: block;
      opacity: 1;
      visibility: visible;
      transform: translateX(-50%) translateY(0);
    }
  }
}


/*loadding-new*/
.loadding-new{
    text-align: center;
    .sk-child {
      margin:0 2px;
      width: 11px;
      height: 11px;
      background-color: $colormain;
      border-radius: 100%;
      display: inline-block;
      -webkit-animation: sk-three-bounce 1.4s ease-in-out 0s infinite both;
      animation: sk-three-bounce 1.4s ease-in-out 0s infinite both;
    }
    .sk-bounce1 {
      -webkit-animation-delay: -0.32s;
      animation-delay: -0.32s;
    }
    .sk-bounce2 {
      -webkit-animation-delay: -0.16s;
      animation-delay: -0.16s;
    }                
}
@-webkit-keyframes sk-three-bounce {
  0%, 80%, 100% {
    -webkit-transform: scale(0);
    transform: scale(0); }
    40% {
      -webkit-transform: scale(1);
      transform: scale(1); } }

      @keyframes sk-three-bounce {
        0%, 80%, 100% {
          -webkit-transform: scale(0);
          transform: scale(0); }
          40% {
            -webkit-transform: scale(1);
            transform: scale(1); } }
/*Icons*/

.ic-live {margin-right: 6px;font-family: arial;font-size: 11px;text-transform: uppercase;height: 15px;line-height: 15px;display: inline-block;vertical-align: middle;color: $colormain;}
.ic-live:before {-webkit-border-radius: 50%;border-radius: 50%;display: block;position: relative;width: 13px;height: 13px;content: ''; background: $colormain;box-shadow: 0 0 0 rgba(23, 143, 214, 0.4); margin-right: 5px;vertical-align: initial;-webkit-animation: live-pulse 1s infinite;-moz-animation: live-pulse 1s infinite;-o-animation: live-pulse 1s infinite;animation: live-pulse 1s infinite;float: left;}
@-webkit-keyframes live-pulse {
  0% {
    -webkit-box-shadow: 0 0 0 0 rgba(23, 143, 214, 0.4);
  }
  70% {
      -webkit-box-shadow: 0 0 0 10px rgba(23, 143, 214, 0);
  }
  100% {
      -webkit-box-shadow: 0 0 0 0 rgba(23, 143, 214, 0);
  }
}
@keyframes live-pulse {
  0% {
    -moz-box-shadow: 0 0 0 0 rgba(23, 143, 214, 0.4);
    box-shadow: 0 0 0 0 rgba(23, 143, 214, 0.4);
  }
  70% {
      -moz-box-shadow: 0 0 0 10px rgba(23, 143, 214, 0);
      box-shadow: 0 0 0 10px rgba(23, 143, 214, 0);
  }
  100% {
      -moz-box-shadow: 0 0 0 0 rgba(23, 143, 214, 0);
      box-shadow: 0 0 0 0 rgba(23, 143, 214, 0);
  }
}